import { ApiProperty, ApiPropertyOptional } from '@nestjs/swagger'
import { CreateGoogleEventReq, EventDate, EventMetaData } from './create-google-event.dto'
import {
  IsBoolean,
  IsEnum,
  IsNotEmptyObject,
  IsObject,
  IsOptional,
  Matches,
  Max,
  Min,
  ValidateNested
} from 'class-validator'
import { Type } from 'class-transformer'

export class GoogleEventReq$Optional extends CreateGoogleEventReq {
  @ApiPropertyOptional({
    description:
      'Khng phi m hex, colorId l s nguyn thuc [0-11]. Gi api: /api/calendar/color  ly danh sch m mu',
    example: '0'
  })
  @Matches(/^([0-9]|1[01])$/, {
    message: 'colorId must be a number in [0-11]'
  })
  @IsOptional()
  colorId?: string

  @ApiPropertyOptional({
    required: true,
    description:
      'Phi c mt trong hai trng: dateTime hoc date. Nu c dateTime th s khng c date. Lu  TimeZone',
    example: {
      dateTime: '2021-08-01T09:00:00+07:00',
      timeZone: 'Asia/Ho_Chi_Minh'
    }
  })
  @IsNotEmptyObject()
  @IsObject()
  @IsOptional()
  @Type(() => EventDate)
  start: EventDate

  @ApiPropertyOptional({
    required: true,
    description:
      'Phi c mt trong hai trng: dateTime hoc date. Nu c dateTime th s khng c date.Lu  TimeZone',
    example: {
      dateTime: '2021-08-01T09:30:00+07:00',
      timeZone: 'Asia/Ho_Chi_Minh'
    }
  })
  @IsNotEmptyObject()
  @IsObject()
  @IsOptional()
  @Type(() => EventDate)
  end: EventDate

  @ApiProperty({
    required: true,
    description: 'Title of Event',
    example: 'Event title'
  })
  @IsOptional()
  summary?: string

  @ApiPropertyOptional({
    description: 'C xa meet hay khng',
    example: true
  })
  @IsOptional()
  @IsBoolean()
  is_remove_meet?: boolean

  @ApiProperty({
    required: true,
    description: 'Meta ca Event. Lu : room_id l bt buc',
    example: {
      room_id: '123456789',
      room_name: '1321'
    }
  })
  @IsObject()
  @Type(() => EventMetaData)
  @ValidateNested()
  @IsOptional()
  extendedProperties: EventMetaData

  @ApiProperty({
    required: true,
    description:
      'single: ch update event ny, all: update tt c cc instance event, follow: update tt c cc event sau event ny',
    example: 'single | all | following'
  })
  @IsEnum(['single', 'all', 'following'], {
    message: 'update_type must be a string in [single, all, following]'
  })
  update_type: string
}

export class UpdateGoogleEventReq extends GoogleEventReq$Optional {}
